<template>
  <div>
    <div style="width:60%;margin:auto;">
      <br>
      <h1></h1>
      <hr>
      <form class="needs-validation" novalidate id='myForm'>
        <label></label>
        <b-form-select v-validate="'required'" name="region" vv-data-name="" v-model="region" :options="options" class="mb-3" />
        <h6 v-show="errors.has('region')" class="alert-empty">{{ errors.first('region')}}</h6>


        <label>(</label>
        <b-form-input v-validate="'required'" name="uniNameCN" data-vv-as="" :class="{'has-error': errors.has('uniNameCN')}" v-model="uniNameCN" type="text" placeholder="" style="width:30%" ></b-form-input>
        <h6 v-show="errors.has('uniNameCN')" class="alert-empty">{{ errors.first('uniNameCN')}}</h6>

        <label>(</label>
        <b-form-input v-validate="'required'" name="uniNameEN" data-vv-as="" :class="{'has-error': errors.has('uniNameEN')}" v-model="uniNameEN" type="text" placeholder="" style="width:30%" ></b-form-input>
        <h6 v-show="errors.has('uniNameEN')" class="alert-empty">{{ errors.first('uniNameEN')}}</h6>

        <label>(</label>
        <b-form-input v-validate="'required'" name="name_cn" data-vv-as="" :class="{'has-error': errors.has('name_cn')}" v-model="name_cn" type="text" placeholder="" style="width:30%" ></b-form-input>
        <h6 v-show="errors.has('name_cn')" class="alert-empty">{{ errors.first('name_cn') }}</h6>
        
        <label>(</label>
        <b-form-input v-validate="'required'" name="name_en" data-vv-as="" :class="{'has-error': errors.has('name_en')}" v-model="name_en" type="text" placeholder="" style="width:30%" ></b-form-input>
        <h6 v-show="errors.has('name_en')" class="alert-empty">{{ errors.first('name_en') }}</h6>

        <label></label>
        <b-form-input v-validate="'required|email'" name="email" data-vv-as="" v-model="email" type="email" placeholder="" style="width:30%" ></b-form-input>
        <h6 v-show="errors.has('email')" class="alert-empty">{{ errors.first('email') }}</h6>

        <label></label>
        <b-form-input v-validate="'required'" name="phone" data-vv-as="" v-model="phone" type="text" placeholder="" style="width:30%" ></b-form-input>
        <h6 v-show="errors.has('phone')" class="alert-empty">{{ errors.first('phone') }}</h6>

        <label></label>
        <b-form-input v-validate="'required'" vv-data-as="password" name="password" v-model="password" type="password" ref="password" placeholder="" style="width:30%" ></b-form-input>
        <h6 v-show="errors.has('password')" class="alert-empty">{{ errors.first('password') }}</h6>        
        <b-form-input v-model="password_confirmation" v-validate="'required|confirmed:password'" vv-data-as="password" name="password_confirmation" type="password" placeholder="" style="width:30%" ></b-form-input>
        <h6 v-show="errors.has('password_confirmation')" class="alert-empty">{{ errors.first('password_confirmation') }}</h6>        
        
        <label></label>
        <b-form-textarea v-validate="'required'" data-vv-as="" name="address" v-model="address" placeholder="" :rows="3" :max-rows="6"> </b-form-textarea>
        <h6 v-show="errors.has('address')" class="alert-empty">{{ errors.first('address') }}</h6>

        <label></label>
        <b-form-input v-validate="'required'" data-vv-as="1" name="title_1" v-model="title_1" type="text" placeholder="" style="width:50%" ></b-form-input>
        <h6 v-show="errors.has('title_1')" class="alert-empty">{{ errors.first('title_1')}}</h6>
        <b-form-input v-validate="'required'" data-vv-as="2" name="title_2" v-model="title_2" type="text" placeholder="" style="width:50%" ></b-form-input>
        <h6 v-show="errors.has('title_2')" class="alert-empty">{{ errors.first('title_2')}}</h6>
        <b-form-input v-validate="'required'" data-vv-as="3" name="title_3" v-model="title_3" type="text" placeholder="" style="width:50%" ></b-form-input>
        <h6 v-show="errors.has('title_3')" class="alert-empty">{{ errors.first('title_3')}}</h6>
        
        <label></label>
        <b-form-file v-model="file" :state="Boolean(file)" placeholder="Choose a file..."></b-form-file>
          <div class="mt-3">Selected file: {{file && file.name}}</div>
        <label></label>
        
        <input  type="checkbox" id="checkbox" v-model="checked">
        <label for="checkbox"></label>

        <b-button :disabled="isDisabled" @click="submitForm">Submit</b-button>
    </form>
      </div>
      

</div>
</template>
<script>
    import axios from "axios";

    export default
{

  name:'register',
  data() {return {
    file: null,
    region: null,
    uniNameCN:'',
    uniNameEN:'',
    name_cn:'',
    name_en:'',
    email:'',
    phone:'',
    password:'',
    address:'',
    title_1:'',
    title_2:'',
    title_3:'',
    checked:false,
    password_confirmation: '',

      options: [
        { value: null, text: '' },
        { value: 'Singapore', text: '' },
        { value: 'Malaysia', text: '' },
        { value: 'China', text: '' },
        { value: 'Hong Kong', text: ''},
        {value:'Macao',text:''},
        {value:'Taiwan',text:''},
        {value:'Australia',text:''}
      ]

  }},
    computed:{
        isDisabled(){
            return (this.region === null || this.checked === false);
        }
    },

    methods:{
        submitForm(){
            this.$validator.validateAll().then(res=>{

                let data={
                    uniNameCN: this.uniNameCN,
                    uniNameEN: this.uniNameEN,
                    email: this.email,
                    nameCn: this.name_cn,
                    nameEn: this.name_en,
                    address: this.address,
                    password: this.password,
                    contactNumber: this.phone,
                    region: this.region,
                    debateQues1: this.title_1,
                    debateQues2: this.title_2,
                    debateQues3: this.title_3,
                };

                if(res){
                    //if user filled in all the information
                    axios
                        .post('api/register',data)
                        .then(response=>{
                            this.info = response.data;
                            console.log(this.info);
                            if(this.info.message === "Duplicate Entry!"){
                                alert("");
                            }else if(this.info.message === "Successfully registered!"){
                                alert("");
                                document.getElementById("myForm").reset();
                            }
                        });

                }else{
                    alert("")
                }

            })
        },
    }
}
</script>
<style scoped>
label{margin-top:20px;}

.alert-empty {
        color: red;
    }

.has-error {
        border-color: red;
    }

span{
    display: inline
}
</style>



